body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    border: 1px solid #999;
    float: left;
    font-size: 8vmin; /*24px;*/
    font-weight: bold;
    line-height: 34px;
    /*height: 54px;*/
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 17vmin;/*54px;*/
    max-width: 60px;
    aspect-ratio : 1 / 1;
  }
  
  .lightsquare {
    background: #fff;
  }
  
  .darksquare {
    background: rgb(17, 145, 34);
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    /*flex-direction: column;*/
    flex-wrap: wrap;
  }
  
  /*.game > div {
  
  }*/
  
  .game-info {
    /*margin-left: 20px;*/
    margin-top: 20px;
  }
  
  ul.no-bullets {
    list-style-type: none; /* Remove bullets */
    /*padding: 0; /* Remove padding */
    /*margin: 0; /* Remove margins */
  }
  
  .info-message {
    margin: 20px;
  }
  
  .button-move {
    width: 125px;
    height: 40px;
    text-align: left;
  }